body {
  margin: 0;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: #F5F4EF;
  margin-bottom: 1000px;
}
img {
  max-width: 100%;
}
h1 {
  font-weight: 600;
}
.container {
  max-width: 1152px;
  margin: 0 auto;
}
:root {
  --transition-duration: 250ms;
  --transition-function: ease-in-out;
  --color-text: #212121;
}
@font-face {
  font-family: 'Montserrat';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Regular.woff2) format('woff2'), url(../fonts/Montserrat-Regular.woff) format('woff');
}
@font-face {
  font-family: 'Montserrat';
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-SemiBold.woff2) format('woff2'), url(../fonts/Montserrat-SemiBold.woff) format('woff');
}
@font-face {
  font-family: 'Random Grotesque Standard';
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/RG-StandardSemibold.woff2) format('woff2'), url(../fonts/RG-StandardSemibold.woff) format('woff');
}
@media (min-width: 990px) {
  .header__container {
    display: grid;
    align-items: center;
    grid-template-columns: 250px 1fr 250px;
    gap: 20px;
  }
}
.nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.nav__right {
  text-align: right;
}
@media (min-width: 990px) {
  .nav__list {
    margin: 0 auto;
    width: fit-content;
  }
  .nav__link {
    padding: 4px;
  }
}
.nav__link {
  position: relative;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: inherit;
  text-transform: lowercase;
  text-decoration: none;
  overflow: hidden;
  display: block;
}
.nav__link::after {
  content: attr(data-hover);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
  transition: transform var(--transition-duration) var(--transition-function);
}
.nav__link span {
  transition: transform var(--transition-duration) var(--transition-function);
  display: block;
}
.nav__link:hover::after {
  transform: translateY(0);
}
.nav__link:hover span {
  transform: translateY(-100%);
}
.list-links {
  display: inline-flex;
  margin: 0;
}
.list-links a {
  display: block;
  margin-right: 8px;
  margin-bottom: 8px;
  font-weight: 600;
  color: inherit;
  text-transform: lowercase;
  text-decoration: none;
  border: 1px solid #212121;
  border-radius: 12px;
}
.button-arrow {
  overflow: hidden;
  position: relative;
  display: inline-block;
  width: 74px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  border: 1px solid var(--color-text);
  border-radius: 12px;
  appearance: none;
}
.button-arrow::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-text);
  mask-position: center;
  mask-repeat: no-repeat;
}
.button-arrow--left::before {
  mask-image: url(../img/arrow-left.svg);
}
.button-arrow--right::before {
  mask-image: url(../img/arrow-right.svg);
}
.portfolio__title {
  position: relative;
  max-width: 660px;
  font-weight: 600;
  font-size: 48px;
  line-height: 1;
  font-family: 'Random Grotesque Standard', Arial, Helvetica, sans-serif;
}
.portfolio__title-animate {
  position: relative;
  display: block;
  height: 48px;
  overflow: hidden;
}
.portfolio__title-animate span {
  --duration: 20s;
  position: absolute;
  top: 0;
  left: 0;
  perspective: 100px;
  opacity: 0;
  animation-name: portfolio-title;
  animation-duration: var(--duration);
  animation-timing-function: var(--transition-function);
  animation-iteration-count: infinite;
}
.portfolio__title-animate span:nth-child(2) {
  animation-delay: calc(var(--duration)/3);
}
.portfolio__title-animate span:nth-child(3) {
  animation-delay: calc(var(--duration)/3 * 2);
}
@keyframes portfolio-title {
  0% {
    transform: rotateX(90deg) translateY(100%);
    opacity: 0;
  }
  1% {
    transform: rotateX(0) translateY(0);
    opacity: 1;
  }
  32% {
    transform: rotateX(0) translateY(0);
    opacity: 1;
  }
  33% {
    transform: rotateX(90deg) translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: rotateX(90deg) translateY(-100%);
    opacity: 0;
  }
}
.services__content {
  position: relative;
  display: flex;
  align-items: flex-start;
}
.services__left {
  position: sticky;
  top: 50vh;
}
.services__right span {
  display: block;
}
.services__button-arrow {
  opacity: 0;
  transition: opacity var(--transition-duration) var(--transition-function);
}
.services__right span:hover .services__button-arrow {
  opacity: 1;
}
